import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Loader from '../../Components/Loader';
import Message from '../../Components/Message';
import { Helmet } from 'react-helmet';
import { Rating } from '@material-ui/lab';
import { Link } from 'react-router-dom';

const Container = styled.div`
    height: calc(100vh-50px);
    width: 100vw;
`;

const Backdrop = styled.div`
    position: fixed;
    top: 50px;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(${(props) =>
        `https://image.tmdb.org/t/p/original${props.bdUrl}`});
    background-position: center center;
    background-size: cover;
    filter: blur(1.5px);
    opacity: 0.5;
    z-index: -2;
`;
const Content = styled.div`
    display: flex;
    height: 100vh;
    width: 100%;
    position: relative;
`;
const CoverContainer = styled.div``;
const Cover = styled.div`
    display: block;
    position: fixed;
    top: 70px;
    left: 30px;
    height: 450px;
    width: 300px;
    background-image: url(${(props) => props.poUrl});
    background-position: center center;
    background-size: cover;
    border-radius: 5px;
`;

const Data = styled.div`
    width: 1200px;
    height: 700px;
    margin-left: 350px;
`;

const Title = styled.span`
    font-size: 50px;
    font-weight: 600;
    color: white;
`;

const ItemContainer = styled.div`
    display: flex;
`;

const Item = styled.span`
    display: flex;
    align-items: center;
`;

const Divider = styled.div`
    margin: 0px 10px;
`;

const Overview = styled.p`
    font-size: 12px;
    opacity: 0.9;
    line-height: 1.5;
    width: 80%;
`;
const Videos = styled.div`
    display: grid;
    align-items: center;
    grid-auto-flow: column;
    grid-auto-columns: 30%; // play with this to change height of the children, 50% will fill half
    // grid-template-columns: unset; // do not set template columns and rows
    // grid-template-rows: unset;
    overflow: scroll;
`;
const SectionTitle = styled.span`
    display: block;
    font-size: 20px;
    margin-top: 20px;
`;

const HR = styled.hr`
    margin: 10px 0px;
`;

const Peoples = styled.div`
    display: grid;
    align-items: center;
    grid-auto-flow: column;
    grid-auto-columns: 12%; // play with this to change height of the children, 50% will fill half
    // grid-template-columns: unset; // do not set template columns and rows
    // grid-template-rows: unset;
    overflow: scroll;
`;

const PeopleCover = styled.div`
    height: 150px;
    width: 100px;
    background-image: url(${(props) => props.profile});
    background-position: center center;
    background-size: cover;
    border-radius: 5px;
    margin-bottom: 5px;
`;
const PeopleName = styled.div`
    font-size: 12px;
    height: 18px;
    margin: 0px;
    padding: 0px;
`;
const CharacterName = styled.div`
    font-size: 12px;
    color: red;
    height: 18px;
    margin: 7px;
    padding: 0px;
`;

const PeopleContainer = styled.div`
    text-align: center;
    &:hover {
        ${PeopleCover} {
            opacity: 0.5;
        }
    }
`;
const LogoContainer = styled.div`
    position: fixed;
    top: 530px;
    width: 280px;
    background-color: white;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    // opacity: 0.3;
    border-radius: 5px;
    padding: 10px;
    align-items: center;
`;

const Logo = styled.img`
    width: 90%;
    opacity: 1;
`;

const CTitle = styled.div`
    color: black;
    font-size: 10px;
`;

const DetailPresenter = ({ result, error, credits, similar, loading }) =>
    loading ? (
        <Loader>
            <title>Detail | OBSOS</title>
        </Loader>
    ) : (
        <Container>
            <Helmet>
                <title>
                    {`${
                        result != null &&
                        (result.title != null ? result.title : result.name)
                    }`}
                </title>
            </Helmet>
            <Backdrop bdUrl={result != null && result.backdrop_path} />
            {error ? <Message text={error} color="tomato" /> : null}
            <Content>
                <CoverContainer>
                    <Cover
                        poUrl={
                            result != null && result.poster_path != null
                                ? `https://image.tmdb.org/t/p/w300${result.poster_path}`
                                : 'data:image/png;base64,'
                        }
                    />
                    {result != null && result.production_companies.length > 0 && (
                        <LogoContainer>
                            {result != null &&
                                result.production_companies.map((company) => {
                                    let url = null;
                                    if (company.logo_path != null) {
                                        url =
                                            'https://image.tmdb.org/t/p/w200' +
                                            company.logo_path;
                                        return <Logo src={url} />;
                                    }
                                    return <CTitle>{company.name}</CTitle>;
                                })}
                        </LogoContainer>
                    )}
                </CoverContainer>
                <Data>
                    <Title>
                        {result != null &&
                            (result.title != null ? result.title : result.name)}
                    </Title>
                    <ItemContainer>
                        <Item>
                            {result != null &&
                                (result.release_date != null
                                    ? result.release_date.substring(0, 4)
                                    : result.release_date != null
                                    ? result.first_air_date.substring(0, 4)
                                    : null)}
                        </Item>
                        <Divider>|</Divider>
                        <Item>
                            {result != null &&
                                (result.runtime != null
                                    ? result.runtime
                                    : result.episode_run_time != null
                                    ? result.episode_run_time
                                    : 0)}{' '}
                            min
                        </Item>
                        <Divider>|</Divider>
                        <Item>
                            <Rating
                                value={
                                    result != null &&
                                    (result.vote_average != null
                                        ? result.vote_average
                                        : null)
                                }
                                max={10}
                                precision={0.5}
                                readOnly
                                size="small"
                            />
                            &nbsp;
                            {result != null &&
                                (result.vote_average != null
                                    ? result.vote_average
                                    : null)}
                            &nbsp;/ 10
                        </Item>
                        <Divider>|</Divider>
                        <Item>
                            {result != null &&
                                (result.genres != null
                                    ? result.genres.map((genre, index) =>
                                          index === result.genres.length - 1
                                              ? genre.name
                                              : genre.name + ' / '
                                      )
                                    : null)}
                        </Item>
                        {result != null &&
                            (result.imdb_id != null ? (
                                <Divider>|</Divider>
                            ) : null)}
                        <Item>
                            {result != null &&
                                (result.imdb_id != null ? (
                                    <a
                                        href={
                                            result != null &&
                                            (result.imdb_id != null
                                                ? `https://www.imdb.com/title/${result.imdb_id}`
                                                : null)
                                        }
                                    >
                                        <img
                                            class="fit-picture"
                                            src="https://m.media-amazon.com/images/G/01/IMDb/BG_rectangle._CB1509060989_SY230_SX307_AL_.png"
                                            alt="Grapefruit slice atop a pile of other slices"
                                            width="30px"
                                        ></img>
                                    </a>
                                ) : null)}
                        </Item>
                    </ItemContainer>

                    <Overview>
                        {result != null &&
                            (result.overview != null ? result.overview : null)}
                    </Overview>
                    {credits != null &&
                        credits.data.cast != null &&
                        credits.data.cast.length > 0 && (
                            <>
                                <SectionTitle>Cast</SectionTitle>
                                <HR />
                            </>
                        )}

                    <Peoples>
                        {credits != null
                            ? credits.data.cast.map((actor) => {
                                  return (
                                      <Link to={`/profile/${actor.id}`}>
                                          <PeopleContainer>
                                              <PeopleCover
                                                  profile={
                                                      actor.profile_path != null
                                                          ? `https://image.tmdb.org/t/p/w200${actor.profile_path}`
                                                          : 'data:image/png;base64,'
                                                  }
                                              />
                                              <PeopleName>
                                                  {actor.name}
                                              </PeopleName>
                                              <CharacterName>
                                                  {actor.character.length > 30
                                                      ? `${actor.character.substring(
                                                            0,
                                                            30
                                                        )}...`
                                                      : actor.character}
                                              </CharacterName>
                                          </PeopleContainer>
                                      </Link>
                                  );
                              })
                            : null}
                    </Peoples>
                    {credits != null &&
                        credits.data.crew != null &&
                        credits.data.crew.length > 0 && (
                            <>
                                <SectionTitle>Crew</SectionTitle>
                                <HR />
                            </>
                        )}

                    <Peoples>
                        {credits != null
                            ? credits.data.crew.map((staff) => {
                                  return (
                                      <Link to={`/profile/${staff.id}`}>
                                          <PeopleContainer>
                                              <PeopleCover
                                                  profile={
                                                      staff.profile_path != null
                                                          ? `https://image.tmdb.org/t/p/w200${staff.profile_path}`
                                                          : 'data:image/png;base64,'
                                                  }
                                              />
                                              <PeopleName>
                                                  {staff.name}
                                              </PeopleName>
                                              <CharacterName>
                                                  {staff.job.length > 30
                                                      ? `${staff.job.substring(
                                                            0,
                                                            30
                                                        )}...`
                                                      : staff.job}
                                              </CharacterName>
                                          </PeopleContainer>
                                      </Link>
                                  );
                              })
                            : null}
                    </Peoples>
                    {result != null &&
                        result.videos != null &&
                        result.videos.results.length > 0 && (
                            <>
                                <SectionTitle>Videos</SectionTitle>
                                <HR />
                            </>
                        )}
                    <Videos>
                        {result != null &&
                            (result.videos != null
                                ? result.videos.results.map((video, index) => {
                                      let url = null;
                                      if (index === 0) {
                                          url =
                                              'https://www.youtube.com/embed/' +
                                              video.key +
                                              '?autoplay=1&loop=1';
                                      } else {
                                          url =
                                              'https://www.youtube.com/embed/' +
                                              video.key;
                                      }
                                      return (
                                          <iframe
                                              width="320px"
                                              height="240px"
                                              key={video.key}
                                              src={url}
                                              title={video.name}
                                          ></iframe>
                                      );
                                  })
                                : 'No Result  ')}
                    </Videos>

                    {result != null && result.belongs_to_collection != null && (
                        <>
                            <SectionTitle>Collection</SectionTitle>
                            <HR />
                        </>
                    )}

                    <Peoples>
                        {result != null &&
                        result.belongs_to_collection != null ? (
                            <Link
                                to={`/collection/${result.belongs_to_collection.id}`}
                            >
                                <PeopleContainer>
                                    <PeopleCover
                                        profile={
                                            result.belongs_to_collection
                                                .poster_path != null
                                                ? `https://image.tmdb.org/t/p/w200${result.belongs_to_collection.poster_path}`
                                                : 'data:image/png;base64,'
                                        }
                                    />
                                    <PeopleName>
                                        {result.belongs_to_collection.name !=
                                        null
                                            ? result.belongs_to_collection.name
                                                  .length > 15
                                                ? `${result.belongs_to_collection.name.substring(
                                                      0,
                                                      15
                                                  )}...`
                                                : result.belongs_to_collection
                                                      .name
                                            : null}
                                    </PeopleName>
                                </PeopleContainer>
                            </Link>
                        ) : null}
                    </Peoples>

                    {result != null &&
                        result.seasons != null &&
                        result.seasons.length > 0 && (
                            <>
                                <SectionTitle>Seasons</SectionTitle>
                                <HR />
                            </>
                        )}

                    <Peoples>
                        {result != null && result.seasons != null
                            ? result.seasons.map((content) => {
                                  return (
                                      <Link
                                          to={`/tv/${result.id}/season/${content.season_number}`}
                                      >
                                          <PeopleContainer>
                                              <PeopleCover
                                                  profile={
                                                      content.poster_path !=
                                                      null
                                                          ? `https://image.tmdb.org/t/p/w200${content.poster_path}`
                                                          : 'data:image/png;base64,'
                                                  }
                                              />
                                              <PeopleName>
                                                  {content.name != null
                                                      ? content.name.length > 15
                                                          ? `${content.name.substring(
                                                                0,
                                                                15
                                                            )}...`
                                                          : content.name
                                                      : null}
                                                  {content.title != null
                                                      ? content.title.length >
                                                        15
                                                          ? `${content.title.substring(
                                                                0,
                                                                15
                                                            )}...`
                                                          : content.title
                                                      : null}
                                              </PeopleName>
                                          </PeopleContainer>
                                      </Link>
                                  );
                              })
                            : null}
                    </Peoples>

                    {similar != null && similar.data.results.length > 0 && (
                        <>
                            <SectionTitle>Similar Contents</SectionTitle>
                            <HR />
                        </>
                    )}

                    <Peoples>
                        {similar != null
                            ? similar.data.results.map((content) => {
                                  return (
                                      <Link
                                          to={
                                              similar.config.url.includes(
                                                  'movie'
                                              )
                                                  ? `/movie/${content.id}`
                                                  : `/show/${content.id}`
                                          }
                                      >
                                          <PeopleContainer>
                                              <PeopleCover
                                                  profile={
                                                      content.poster_path !=
                                                      null
                                                          ? `https://image.tmdb.org/t/p/w200${content.poster_path}`
                                                          : 'data:image/png;base64,'
                                                  }
                                              />
                                              <PeopleName>
                                                  {content.name != null
                                                      ? content.name.length > 15
                                                          ? `${content.name.substring(
                                                                0,
                                                                15
                                                            )}...`
                                                          : content.name
                                                      : null}
                                                  {content.title != null
                                                      ? content.title.length >
                                                        15
                                                          ? `${content.title.substring(
                                                                0,
                                                                15
                                                            )}...`
                                                          : content.title
                                                      : null}
                                              </PeopleName>
                                          </PeopleContainer>
                                      </Link>
                                  );
                              })
                            : null}
                    </Peoples>
                </Data>
            </Content>
        </Container>
    );

DetailPresenter.propTypes = {
    result: PropTypes.array,
    url: PropTypes.string,
    error: PropTypes.string,
    loading: PropTypes.bool.isRequired,
};

export default DetailPresenter;
