import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import Loader from '../../Components/Loader';
import Message from '../../Components/Message';
import { Helmet } from 'react-helmet';

import { Link } from 'react-router-dom';

const Container = styled.div`
    height: calc(100vh-50px);
    width: 100vw;
`;

const Content = styled.div`
    display: flex;
    height: 100vh;
    width: 100%;
    position: relative;
`;
const CoverContainer = styled.div``;
const Cover = styled.div`
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    height: 450px;
    width: 300px;
    background-image: url(${(props) =>
        `https://image.tmdb.org/t/p/w300${props.poUrl}`});
    background-position: center center;
    background-size: cover;
    border-radius: 5px;
`;

const Data = styled.div`
    width: 1200px;
    height: 700px;
    margin-left: 350px;
`;

const Title = styled.span`
    font-size: 50px;
    font-weight: 600;
    color: white;
`;

const ItemContainer = styled.div`
    display: flex;
`;

const Item = styled.span`
    display: flex;
    align-items: center;
`;

const Divider = styled.div`
    margin: 0px 10px;
`;

const Overview = styled.p`
    font-size: 12px;
    opacity: 0.9;
    line-height: 1.5;
    width: 80%;
`;

const SectionTitle = styled.span`
    display: block;
    font-size: 20px;
    margin-top: 20px;
`;

const HR = styled.hr`
    margin: 10px 0px;
`;

const Credits = styled.div`
    display: grid;
    align-items: center;
    grid-auto-flow: column;
    grid-auto-columns: 12%; // play with this to change height of the children, 50% will fill half
    // grid-template-columns: unset; // do not set template columns and rows
    // grid-template-rows: unset;
    overflow: scroll;
`;

const CreditCover = styled.div`
    height: 150px;
    width: 100px;
    background-image: url(${(props) => props.poster});
    background-position: center center;
    background-size: cover;
    border-radius: 5px;
    margin-bottom: 5px;
`;
const CreditTitle = styled.div`
    font-size: 12px;
    height: 18px;
    margin: 0px;
    padding: 0px;
`;

const CharacterName = styled.div`
    font-size: 12px;
    color: red;
    height: 18px;
    margin: 0px;
    padding: 0px;
`;
const CreditContainer = styled.div`
    text-align: center;
    &:hover {
        ${CreditCover} {
            opacity: 0.5;
        }
    }
`;

const ProfilePresenter = ({
    result,
    error,
    loading,
    tvCredits,
    movieCredits,
}) =>
    loading ? (
        <Loader>
            <title>Profile | OBSOS</title>
        </Loader>
    ) : (
        <Container>
            <Helmet>
                <title>
                    {`${
                        result != null &&
                        (result.title != null ? result.title : result.name)
                    }`}
                </title>
            </Helmet>
            {/* <Backdrop bdUrl={result != null && result.backdrop_path} /> */}
            {error ? <Message text={error} color="tomato" /> : null}
            <Content>
                <CoverContainer>
                    <Cover poUrl={result != null && result.profile_path} />
                    {/* <Item>Also Known As</Item> */}
                </CoverContainer>
                <Data>
                    <Title>
                        {result != null &&
                            (result.title != null ? result.title : result.name)}
                    </Title>
                    <ItemContainer>
                        <Item>
                            {result != null &&
                                (result.known_for_department != null
                                    ? result.known_for_department
                                    : null)}
                        </Item>
                        {result != null &&
                            (result.place_of_birth != null ? (
                                <Divider>|</Divider>
                            ) : null)}
                        <Item>
                            {result != null &&
                                (result.place_of_birth != null
                                    ? result.place_of_birth
                                    : null)}
                        </Item>
                        {result != null &&
                            (result.imdb_id != null ? (
                                <Divider>|</Divider>
                            ) : null)}
                        <Item>
                            {result != null &&
                                (result.imdb_id != null ? (
                                    <a
                                        href={
                                            result != null &&
                                            (result.imdb_id != null
                                                ? `https://www.imdb.com/name/${result.imdb_id}`
                                                : null)
                                        }
                                    >
                                        <img
                                            class="fit-picture"
                                            src="https://m.media-amazon.com/images/G/01/IMDb/BG_rectangle._CB1509060989_SY230_SX307_AL_.png"
                                            alt="Grapefruit slice atop a pile of other slices"
                                            width="30px"
                                        ></img>
                                    </a>
                                ) : null)}
                        </Item>
                    </ItemContainer>

                    <Overview>
                        {result != null &&
                            (result.biography != null
                                ? result.biography
                                : null)}
                    </Overview>
                    {movieCredits != null &&
                        movieCredits.cast != null &&
                        movieCredits.cast.length > 0 && (
                            <>
                                <SectionTitle>Movies - Cast</SectionTitle>
                                <HR />
                            </>
                        )}
                    <Credits>
                        {movieCredits != null &&
                            (movieCredits.cast != null
                                ? movieCredits.cast.map((movie) => {
                                      return (
                                          <Link to={`/movie/${movie.id}`}>
                                              <CreditContainer>
                                                  <CreditCover
                                                      poster={
                                                          movie.poster_path !=
                                                          null
                                                              ? `https://image.tmdb.org/t/p/w200${movie.poster_path}`
                                                              : 'data:image/png;base64,'
                                                      }
                                                  />
                                                  <CreditTitle>
                                                      {movie.title != null
                                                          ? movie.title.length >
                                                            15
                                                              ? `${movie.title.substring(
                                                                    0,
                                                                    15
                                                                )}...`
                                                              : movie.title
                                                          : null}
                                                  </CreditTitle>
                                                  <CharacterName>
                                                      {movie.character != null
                                                          ? movie.character
                                                                .length > 15
                                                              ? `${movie.character.substring(
                                                                    0,
                                                                    15
                                                                )}...`
                                                              : movie.character
                                                          : null}
                                                  </CharacterName>
                                              </CreditContainer>
                                          </Link>
                                      );
                                  })
                                : null)}
                    </Credits>
                    {movieCredits != null &&
                        movieCredits.crew != null &&
                        movieCredits.crew.length > 0 && (
                            <>
                                <SectionTitle>Movies - Crew</SectionTitle>
                                <HR />
                            </>
                        )}
                    <Credits>
                        {movieCredits != null &&
                            (movieCredits.crew != null
                                ? movieCredits.crew.map((movie) => {
                                      return (
                                          <Link to={`/movie/${movie.id}`}>
                                              <CreditContainer>
                                                  <CreditCover
                                                      poster={
                                                          movie.poster_path !=
                                                          null
                                                              ? `https://image.tmdb.org/t/p/w200${movie.poster_path}`
                                                              : 'data:image/png;base64,'
                                                      }
                                                  />
                                                  <CreditTitle>
                                                      {movie.title != null
                                                          ? movie.title.length >
                                                            15
                                                              ? `${movie.title.substring(
                                                                    0,
                                                                    15
                                                                )}...`
                                                              : movie.title
                                                          : null}
                                                  </CreditTitle>
                                                  <CharacterName>
                                                      {movie.job != null
                                                          ? movie.job.length >
                                                            15
                                                              ? `${movie.job.substring(
                                                                    0,
                                                                    15
                                                                )}...`
                                                              : movie.job
                                                          : null}
                                                  </CharacterName>
                                              </CreditContainer>
                                          </Link>
                                      );
                                  })
                                : null)}
                    </Credits>
                    {tvCredits != null &&
                        tvCredits.cast != null &&
                        tvCredits.cast.length > 0 && (
                            <>
                                <SectionTitle>TV Shows - Cast</SectionTitle>
                                <HR />
                            </>
                        )}
                    <Credits>
                        {tvCredits != null &&
                            (tvCredits.cast != null
                                ? tvCredits.cast.map((show) => {
                                      return (
                                          <Link to={`/show/${show.id}`}>
                                              <CreditContainer>
                                                  <CreditCover
                                                      poster={
                                                          show.poster_path !=
                                                          null
                                                              ? `https://image.tmdb.org/t/p/w200${show.poster_path}`
                                                              : 'data:image/png;base64,'
                                                      }
                                                  />
                                                  <CreditTitle>
                                                      {show.name != null
                                                          ? show.name.length >
                                                            15
                                                              ? `${show.name.substring(
                                                                    0,
                                                                    15
                                                                )}...`
                                                              : show.name
                                                          : null}
                                                  </CreditTitle>
                                                  <CharacterName>
                                                      {show.character != null
                                                          ? show.character
                                                                .length > 15
                                                              ? `${show.character.substring(
                                                                    0,
                                                                    15
                                                                )}...`
                                                              : show.character
                                                          : null}
                                                  </CharacterName>
                                              </CreditContainer>
                                          </Link>
                                      );
                                  })
                                : null)}
                    </Credits>
                    {tvCredits != null &&
                        tvCredits.crew != null &&
                        tvCredits.crew.length > 0 && (
                            <>
                                <SectionTitle>TV Shows - Crew</SectionTitle>
                                <HR />
                            </>
                        )}
                    <Credits>
                        {tvCredits != null &&
                            (tvCredits.crew != null
                                ? tvCredits.crew.map((show) => {
                                      return (
                                          <Link to={`/show/${show.id}`}>
                                              <CreditContainer>
                                                  <CreditCover
                                                      poster={
                                                          show.poster_path !=
                                                          null
                                                              ? `https://image.tmdb.org/t/p/w200${show.poster_path}`
                                                              : 'data:image/png;base64,'
                                                      }
                                                  />
                                                  <CreditTitle>
                                                      {show.name != null
                                                          ? show.name.length >
                                                            15
                                                              ? `${show.name.substring(
                                                                    0,
                                                                    15
                                                                )}...`
                                                              : show.name
                                                          : null}
                                                  </CreditTitle>
                                                  <CharacterName>
                                                      {show.job != null
                                                          ? show.job.length > 15
                                                              ? `${show.job.substring(
                                                                    0,
                                                                    15
                                                                )}...`
                                                              : show.job
                                                          : null}
                                                  </CharacterName>
                                              </CreditContainer>
                                          </Link>
                                      );
                                  })
                                : null)}
                    </Credits>
                </Data>
            </Content>
        </Container>
    );

ProfilePresenter.propTypes = {
    result: PropTypes.array,
    url: PropTypes.string,
    error: PropTypes.string,
    loading: PropTypes.bool.isRequired,
};

export default ProfilePresenter;
